@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600&display=swap);
h1,h2,h3,h4,h5,h6{font-family:"Rubik",sans-serif;font-weight:400}p,input,button,li,a{font-family:"Rubik",sans-serif;font-weight:600}html,body,#root{height:100%;overflow:hidden}body{margin:0;color:white}button{background:none;color:white;border:none;cursor:pointer;display:flex}.app-container{height:100%;max-height:100%;background:#2b2f38;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.app-container .controls{display:flex;flex-direction:row;align-items:center}.timer{width:100%;max-width:300px;height:300px;display:flex;justify-content:center}.timer .timer-button{flex-direction:column;justify-content:center;align-items:center;width:100%;position:relative}.timer-dial{position:absolute;width:100%;height:100%}.control-sets{display:flex;flex-direction:row}

